.page-header {
    width:100%;
    height: 100px;
    position: fixed;
    top:0;
    left:0;
    z-index: 1000;
    transition: 1s all;
    background-color: #333D69;
}

.page-header.sticky {
    background: darken(mix($color1, $color2), 25%);
    height: 100px;
    box-shadow: 0 1px 3px rgba(black, 0.4);
}

.page-header .container {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo {
}

.logo-img {
    display: block;
    max-height: 100px;
}

.nav {
}

.nav-list {
    list-style:none;
    padding:0;
    margin:0;
    display: flex;
    align-items:center;
}

.nav-list li {
}

.nav-list a {
    display: block;
    padding:15px 20px;
    text-transform: uppercase;
    text-decoration: none;
    font-weight: bold;
    color: rgba(#fff, 0.8);
    position: relative;
    transition:0.5s all;
}

.nav-list a:before {
    content:'';
    position: absolute;
    height:2px;
    right:50%;
    bottom:0;
    left:50%;
    background: #fff;
    transition:0.5s all;
}

.nav-list a:hover {
    color:#fff;
}

.nav-list a:hover:before {
    right:0;
    left:0;
}
